h2 {
    font-family: var(--font);
    font-size: 1.5rem;
    color: white;
    align-self: center;
    justify-self: center;
    font-weight: 100;
}
.TodoCounter{
    text-align: center;
    font-size: var(--title-size);
    font-family: var(--font);
    font-weight: bold;
    color: whitesmoke;
    text-shadow: 0 0 2px whitesmoke;
}
.TodoSearch{
    width: 200px;
    margin-left: calc(50% - 100px);
    padding: 8px;
    font-size: var(--sub-title-size);
    font-weight: lighter;
    color: rgba(255, 255, 255, 0.853);
    border-radius: 8px;
    border: 3px solid var(--purple-color);
    box-shadow: 0px 1px 30px var(--purple-color);
    background-color: var(--purple-color);
}

::-webkit-input-placeholder{
    color: var(--gray-premium-color);
    text-shadow: 0 0 2px var(--gray-premium-color);

}

::placeholder{
    color: var(--gray-premium-color);
    text-shadow: 0 0 2px var(--gray-premium-color);

}


.TodoList{
    display: flex;
    flex-direction: column;
    list-style: none;
    justify-content: center;
    padding: 0;
    margin: 20px 350px

}

@media (max-width: 1200px) {
    .TodoList{
        background-color: transparent;
        margin: 20px 250px;
    }
}

@media (max-width: 950px) {
    .TodoList{
        background-color: transparent;
        margin: 20px 150px;
    }
}

@media (max-width: 780px) {
    .TodoList{
        background-color: transparent;
        margin: 20px 50px;
    }
}

@media (max-width: 530px) {
    .TodoList{
        background-color: transparent;
        margin: 20px 15px;
    }
}

.TodoItem{
    margin: 4px 5px;
    border-radius: 2px;
    background-color: var(--blue-color);
    color: whitesmoke;
    padding: 10px;
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    place-items: center;
    grid-column-gap: 5px;
    -webkit-column-gap: 5px;
            column-gap: 5px;
    font-family: var(--font);
    font-size: var(--task-text-size);
    box-shadow: 0px 0px 10px var(--purple-color);
}

.TodoItem:nth-child(2){
    text-shadow: 0 0 2px var(--gray-premium-color);
}
.TodoItem :nth-child(3):hover{
    content: url(https://cdn.discordapp.com/attachments/831560691252068372/1002767703490187395/x-mark-24.png);
}
.TodoItemDone{
    text-decoration: line-through;
    box-shadow: 0px 5px 50px var(--blue-color);
}

@media (max-width: 780px) {
    .TodoItem{
        font-size: calc(var(--task-text-size) - 0.1rem);
    }
    .TodoItem :nth-child(1){
        width: 50px;
    }
}

@media (max-width: 530px) {
    .TodoItem{
        font-size: calc(var(--task-text-size) - 0.2rem);
    }
    .TodoItem :nth-child(1){
        width: 30px;
    }
    .TodoItem :nth-child(3){
        width: 15px;
    }
}

.CreateButton{
    border: 1px solid var(--blackblue-color);
    box-shadow: 0px 1px 20px var(--blue-color);
    border-radius: 50%;
    width: 60px;
    height: 60px;
    font-size: var(--title-size);
    background-color: var(--purple-color);
    position: fixed;
    bottom: 10px;
    right: 10px;
    -webkit-transform: rotate(0);
            transform: rotate(0);
    transition: 0.4s ease;
    z-index: 2;
    text-shadow: 0 0 2px var(--blackblue-color);
}

.CreateButton:hover {
    -webkit-transform: rotate(224deg);
            transform: rotate(224deg);
}
.modalStyle {
    background: rgba(32,35,41,.8);
    font-family: var(--font);
    font-size: var(--title-size);
    position: fixed;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
  }
form {
    width: 90%;
    max-width: 300px;
    background-color: var(--blackblue-color);
    padding: 33px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-family: var(--font);
    border-radius: 10px;
  }
  
  label {
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    color: whitesmoke;
    margin-bottom: 26px;
  }
  
  textarea {
    background-color: var(--gray-premium-color);
    border: 2px solid var(--purple-color);
    border-radius: 2px;
    box-shadow: 0px 5px 50px var(--blue-color);
    color: #1E1E1F;
    font-size: 20px;
    font-family: var(--font);
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100% - 25px);
  }
  
  textarea::-webkit-input-placeholder {
    color: #5a5a5a;
    font-family: var(--font);
    font-weight: 300;
  }
  
  textarea::placeholder {
    color: #5a5a5a;
    font-family: var(--font);
    font-weight: 300;
  }
  
  textarea:focus {
    outline-color: #61DAFA;
  }
  
  .TodoForm-buttonContainer {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  
  .TodoForm-button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: whitesmoke;
    font-weight: 100;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    border: none;
  }
  
  .TodoForm-button--add {
    background: var(--blue-color);
    box-shadow: 0px 5px 25px var(--blue-color);
  }
  
  .TodoForm-button--cancel {
    background: transparent;
    border: 1px solid var(--blue-color);
    color: var(--blue-color);
  }


@media (max-width: 780px) {

    svg {
        width: 540px;
        height: 80px;
    }
    rect{
        width: 200%;
        height: 150%;
    }
}

@media (max-width: 530px) {
    svg {
        width: 320px;
        height: 80px;
    }
    rect{
        width: 200%;
        height: 150%;
    }
}

:root {
  --main-bg-color: brown;
  --title-size: 2rem;
  --sub-title-size: 1.4rem;
  --task-text-size: 1.3rem;
  --font: 'Poppins', sans-serif;
  --blackblue-color: #0E0B16;
  --purple-color: #A239CA;
  --blue-color: #4717F6;
  --gray-premium-color: #E7DFDD;
}

body{
  background-color: #0E0B16;
  background-color: var(--blackblue-color);
}
